import './Slider.scss';

// import Swiper core and required modules
import SwiperCore, { Navigation, Pagination } from 'swiper';

import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/scrollbar/scrollbar.scss';
import SliderItem from '../sliderItem/SliderItem';

// install Swiper modules
SwiperCore.use([Navigation, Pagination]);

function Slider() {
   return (

      <Swiper
      spaceBetween={50}
      slidesPerView={1}
      navigation
      pagination={{ clickable: true }}
      scrollbar={{ draggable: true }}
      onSwiper={(swiper) => console.log(swiper)}
      onSlideChange={() => console.log('slide change')}
      >
         <SwiperSlide><SliderItem number="1" title="Где встречаемся?" subscr="Москва" /></SwiperSlide>
         <SwiperSlide><SliderItem number="2" title="Время съемки" subscr="3 часа" /></SwiperSlide>
         <SwiperSlide><SliderItem number="3" title="Мест осталось:" subscr="2 места" /></SwiperSlide>
      </Swiper>
   );
}

export default Slider;