
import Slider from '../../slider/Slider';
import s from './AddMap.module.scss';
import cardImg from './../../../assets/img/about/owner.png';


function AddMap() {
   return (
      <section className={s.map}>
         <div className={s.wrap}>
            <iframe
               src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d5794.265529115144!2d27.60260094964794!3d53.917398229100975!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x46dbcfa9febd59bb%3A0x5e4773aba2f04a7a!2sIT-incubator!5e0!3m2!1sru!2sby!4v1620998982585!5m2!1sru!2sby" loading="lazy" allowfullscreen=""  >
            </iframe>
            <div className={s.adress}>
               <Slider />
               <div className={s.card}>
                  <img className={s.cardImg} src={cardImg} alt="owner-img" />
                  <div className={s.cardTxt}>
                     <h2 className={s.cardName}  >Марина Иванова</h2>
                     <h3 className={s.cardJob} >фотограф</h3>
                     <a className={s.cardLink} href="#" >Показать больше информации  о фотографе</a>
                  </div>
               </div>
            </div>
         </div>

      </section>
   );
}

export default AddMap;