import s from './Footer.module.scss';
import logo from './../../../assets/img/logo.svg';
import vk from './../../../assets/img/icons/vk.png';
import fb from './../../../assets/img/icons/facebook.png';
import inst from './../../../assets/img/icons/instragram.png';
import mastercard from './../../../assets/img/icons/mastercard.png';
import visa from './../../../assets/img/icons/visa.png';

function Footer(props) {
   return (
      <footer className={s.footer}>
         <div className={s.top}>
            <div className={s.conteiner}>
               <div className={s.topInner}>
                  <div className={s.topLeft}>
                     <img className={s.logo} src={logo} alt="logoIcon" />
                  </div>
                  <div className={s.topRight}>
                     <ul className={s.social}>
                        <li className={s.socialItem}>
                           <img src={vk} alt="vk-icon" className={s.socialImg} />
                        </li>
                        <li className={s.socialItem}>
                           <img src={fb} alt="facebook-icon" className={s.socialImg} />
                        </li>
                        <li className={s.socialItem}>
                           <img src={inst} alt="instagram-icon" className={s.socialImg} />
                        </li>

                        <li className={s.socialItem}>
                           <div className={s.socialLangList}>
                              <select name="language" >
                                 <option value="ru">ru</option>
                                 <option value="en">en</option>
                              </select>
                           </div>
                        </li>
                     </ul>
                  </div>
               </div>
            </div>
            <div className={s.bottom}>
               <div className={s.conteiner}>
                  <div className={s.bottomInner}>
                     <ul className={s.cardList}>
                        <li className={s.cardItem}>
                           <img src={mastercard} alt="master-card-logo" className={s.cardImg} />
                        </li>
                        <li className={s.cardItem}>
                           <img src={visa} alt="visa-logo" className={s.cardImg} />
                        </li>
                     </ul>
                     <div className={s.text}>
                        <p className={s.pg1}>Тех. поддержка: 8(800)700-00-00</p>
                        <p className={s.pg2}>© 2018 HELYFLY. All Rights Reserved.</p>
                     </div>
                     <div className={s.langList}>
                        <select name="language" >
                           <option value="ru">ru</option>
                           <option value="en">en</option>
                        </select>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </footer >
   )
}
export default Footer;
