import s from './Comment.module.scss';
import star from './../../assets/img/icons/star.svg';

function Comment(props) {
   return (
      <div className={s.comment}>
         <div className={s.person}>
            <img className={s.personImg} src={props.img} alt="userImg" />
            <div className={s.personWrap}>
               <h3 className={s.title}>{props.title}</h3>
               <span className={s.job}>{props.job}</span>
               <div className={s.stars}>
                  <ul className={s.list}>
                     <li className={s.item}>
                        <img src={star} alt="starIcon" className="itemImg" />
                     </li>
                     <li className={s.item}>
                        <img src={star} alt="starIcon" className="itemImg" />
                     </li>
                     <li className={s.item}>
                        <img src={star} alt="starIcon" className="itemImg" />
                     </li>
                     <li className={s.item}>
                        <img src={star} alt="starIcon" className="itemImg" />
                     </li>
                     <li className={s.item}>
                        <img src={star} alt="starIcon" className="itemImg" />
                     </li>
                  </ul>
               </div>
            </div>
            <div className={s.impresWrap}>
               <div className={s.impress}>Супер! Я бы заказала съемку снова!</div>

            </div>
         </div>


         <div className={s.textBlock}>
            <div className={s.text}>{props.text}</div>
            <div className={s.date}>{props.date}</div>
         </div>
      </div>
   )
}
export default Comment;
