import Comment from '../../comment/Comment';
import s from './Feedback.module.scss';
import user1 from './../../../assets/img/сomment/user1.png';
import user2 from './../../../assets/img/сomment/user2.png';
import user3 from './../../../assets/img/сomment/user3.png';

function Feedback(props) {
   return (
      <section className={s.feedback}>
         <div className={s.conteiner}>
            <h2 className={s.title}>Отзывы<span>:</span></h2>
            <Comment title="Валерия Антонова" img={user1} job="Арт-директор"
               text="Хотим выразить огромную благодарность вашей компании! Получили море ощущений, все прошло просто замечательно. Фотки получились великолепные, отдельное спасибо фотографу!" date="27 Апреля, 2019г." />
            <Comment title="Виталий Петров" img={user2} job="Фотограф"
               text="Восторг! :) Очень понравилось :) Захватили последний теплый, солнечный октябрьский день :) Впечатлило всё: сам вертолет, взлет, полёт, приземление. 
            Организация полёта отличная. Спасибо!" date="19 Апреля, 2019г." />
            <Comment title="Джим Керри" img={user3}
               text="Круто-круто-круто!!! Отмечали мой ДэРэ. Отлично провели время!!! Кстати закуски были вкусными и сытными))Мы 
            с собой ничего не брали. Сами много фоткались,поэтому воспоминания обалденные!!!)) А еще нам всем напечатали по сертификату воздухоплавателя, правда некоторые ленятся за ним съездить))) Но все равно приятно. Короче, Презентстар – проверено. Оценка: пять!" date="11 Апреля, 2019г." />

            <div className={s.showMore}>
               <button className={s.btn}>{props.btn}</button>
            </div>
         </div>
      </section>
   )
}
export default Feedback;
