import Interval from '../../interval/Interval';
import Reviews from '../../reviews/Reviews';
import s from './Schedule.module.scss';


function Schedule() {
   return (
      <section className={s.schedule}>
         <div className={s.conteiner}>
            <h2 className={s.title}>Доступная съемка <br /> в ближайшее время:</h2>
            <div className={s.wrap}>
               <div className={s.date}><span>Пятница, 14 сентября</span>   </div>
               <Interval time="15:00 - 18:00" price="1500₽" price2="850₽" place="Осталось — 1 место" />
               <Interval time="19:00 - 22:00" price="1500₽" price2="850₽" place="Осталось — 1 место" />
               <div className={s.reviews}>
                  <Reviews style={{ padding: "0px 0px 27px 0px" }} />
               </div>

            </div>
         </div>
      </section>
   );
}

export default Schedule;